export default {
    name: "loadingImg",
  props:['src','type'],
  data(){
      return{
        imgUrl:'../../static/img/static/defult.png',
        userImg: '../../static/img/static/user.png',
        loading:true
      }
  },
  watch:{
    src:function(){
     this.change()
    }
  },
  methods:{
    toPublish(){
      this.$emit('toPublish')
    },
      change(){
        var newImg = new Image();
        newImg.src = this.src;
        newImg.onerror = () => {  // 
          if(this.type == '6'){
            newImg.src = this.userImg
          }else{
            newImg.src = this.imgUrl;
          }
          this.loading = true
        };
        newImg.onload = () => { // img
          this.imgUrl = newImg.src;
          this.loading = false
        }
      }
  },
  mounted() {
  this.change()
  }
}



