  import '../../static/css/home.css'
	import Head from "../public/head.vue"
	import Detail from "../public/detail.vue"
  import Foot from "../public/foot.vue"
  import test from '../../static/js/language'
  import Loading from '../public/Loading'
  import loadingImg from '../public/loadingImg'
export default {
  name: 'HelloWorld',
  data () {
    return {
      api: this.GLOBAL.baseURL,
      LoadingState: false,
      LoadingState2:false,
      home:true,
      hotIndex:'',
      isSerch:false,
      search:'',
      loading:false,
			bannerList:[],
			recentlyList:[],
			searchList:[],
			hotnav:[],
			hotList:[],
			wonderfulList:[],
      serchList:[],
      language:[],
      isBanner:false,
      editSearch:'',
      content:'',
      activeList:[],
      pubLish:'/publishPage?information=',
      islookRecent:true,
      isserchRry:true,
      ishotPlace:true,
      ishightAc:true,
      isWonderStory:true,
      isLoading:true,
      loadingActive:false
    }
  },
	components:{
		Head,
		Detail,
    Foot,
    Loading,
    loadingImg
	},
  watch:{
  },
  methods: {
    toaPublish(item,index){
      this.$router.push({
        path: '/publishPage',
        query: {
          information: item.activity_id
        }
      })
    },
    toActive(){
      this.$router.push('/experienceCentre')
    },
    getHotActive(){
      this.loadingActive = true
      this.$http.post(this.api + '/home/Activity/activ_list',{
        token: localStorage.getItem('token'),
        sort:1,
        page: 1
      })
        .then(res=>{
          if(res.data.code == 1){
            this.activeList = res.data.data.data
            this.loadingActive = false
          }else if(res.data.code == 3){
            this.getHotActive()
          }else if(res.data.code == 0){
            alert(res.data.msg)
          }
        })
    },
    getVal(msg){
      this.content = msg
    },
    gosearch(){
      if(event.keyCode === 13){
        this.toSearch()
      }
    },
    getKind(){
      this.isserchRry = true
      this.$http.post(this.api + '/home/Kind/kindlist',{
        token: localStorage.getItem('token')
      })
        .then(res=>{
          if(res.data.code == 1){
            let data = res.data.data
            let a = []
            let b = []
            for(let j in data){
              a.push(data[j])
            }
            for(let i =0;i<a.length;i++){
              b.push({img:a[i].image_id?a[i].domain+a[i].image_url:'',title:a[i].kind_name,value:a[i].kind_id})
            }
            this.searchList = b
            this.isserchRry = false
          }else if(res.data.code == 3){
            this.getKind()
          }else if(res.data.code == 0){
            alert(res.data.msg)
            this.isserchRry = false
          }
        })
    },
    searchHome(){
      this.$router.push({
        name: 'homeSearch',
        query:{
          key: this.content,
          type:0
        }
      })
    },
    toptop(item){
      this.$router.push({
        path: '/classification',
        query:{
          information: item.value,
          name: item.title
        }
      })
    },
    handleClick(tab, event){
      this.getActive(this.hotIndex)
    },
    toPublish(item,index) {
      this.$router.push({
        path: '/publishPage',
        query: {
          information: item.activity_id
        }
      })
    },
    toperson(item,index){
      this.$router.push({
        path: '/userdata',
        query: {
          information: item.userId
        }
      })
    },
    consult(index) {
      if (localStorage.getItem('isLogin')) {
        this.$router.push({
          path: '/counsel',
          query: {
            information: this.hotList[index]
          }
        })
      } else {
        this.$alert('')
      }
    },
    toSearch() {
      this.$router.push({
        name: 'homeSearch',
        query:{
          key: this.search,
          type:0
        }
      })
    },
    toStory(item,index){
      this.$router.push({
        path: '/storyPage',
        query:{
          information: item.story_id
        }
      })
    },
    
    showHead(){
      var winHeight =  document.documentElement.clientHeight
      var hotPlace = document.getElementById('hotPlace').offsetTop
      var hightAc = document.getElementById('hightAc').offsetTop
      var WonderStory = document.getElementById('WonderStory').offsetTop
        let a =  window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
        if(a > 533){
          this.home = false
        }else{
          this.home = true
        }
        if(localStorage.getItem('token')){

          if(hotPlace - a <= winHeight){
            if(this.ishotPlace){
              this.ishotPlace = false
              this.getHotLocal()
            }
          }
          if(hightAc - a <= winHeight){
            if(this.ishightAc){
              this.ishightAc = false
              this.getHotActive()
            }
          }
          if(WonderStory - a <= winHeight){
            if(this.isWonderStory){
              this.isWonderStory = false
              this.getStory()
            }
          }
        }
    },
    //
    addZan(item,index){
      if(item.zanIndex == 0){
        this.$http.post(this.api + '/home/Comment/praise',{
          token: localStorage.getItem('token'),
          flag: 1,
          table_id: item.story_id,
          type: 1
        })
          .then(res=>{
           if(res.data.code == 1){
             item.zanIndex = 1
             item.zan = item.zan + 1
           }else if(res.data.code == 3){
             this.addZan(item,index)
           }else if(res.data.code == 0){
             alert(res.data.msg)
           }
          })

      }else{
        this.$http.post(this.api + '/home/Comment/praise',{
          token: localStorage.getItem('token'),
          flag: 1,
          table_id: item.story_id,
          type: 2
        })
          .then(res=>{
            if(res.data.code == 1){
              item.zan = item.zan - 1
              item.zanIndex = 0
            }else if(res.data.code == 3){
              this.addZan(item,index)
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      }
    },
    serchHistory(){
      if(localStorage.getItem('isLogin')){
        this.isSerch = true
        this.$http.post(this.api + '/home/Index/search_lately',{
          token: localStorage.getItem('token')
        })
          .then(res=>{
            if(res.data.code == 1){
              this.serchList = res.data.data
            }else if(res.data.code == 3){
              this.serchHistory()
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      }
    },
    changeSearch(item,index){
      this.$router.push({
        name:'homeSearch',
        query:{
          key: item.keywords
        }
      })
    },
    moreActive(){
      for(let i = 0;i<this.hotnav.length;i++){
        if(this.hotnav[i].city == this.hotIndex){
          this.$router.push({
            path: 'moreActive',
            query:{
              key: this.hotIndex,
            }
          })
        }
      }
    },
    moreStory(){
      this.$router.push('/story')
    },
   getVist(){
     this.islookRecent = true
      if(localStorage.getItem('isLogin')){
        this.$http.post(this.api + '/home/Index/visit_lately',{
          token: localStorage.getItem('token'),
          page: 1
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data.data
              for(let i =0;i<data.length;i++){
                if(data[i].flag == 1){
                  let kind = ''
                  for(let j = 0;j<data[i].kind.length;j++){
                    if(j == data[i].kind.length-1){
                      kind += data[i].kind[j].kind_name
                    }else{
                      kind += data[i].kind[j].kind_name + '/'
                    }
                  }
                  if(!data[i].status){
                    this.recentlyList.push({
                      img:data[i].domain + data[i].image_url,
                      title:data[i].title,
                      activity_id:data[i].table_id,
                      kind: kind,
                      create_time: data[i].create_time,
                      status: data[i].status
                    })
                  }
                }
              }
              this.islookRecent = false
            }else if(res.data.code == 3){
              this.getVist()
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.islookRecent = false
            }
          })
      }else{
        this.recentlyList = []
        this.islookRecent = false
      }
    },
    getActive(val){
      this.LoadingState = true
      this.$http.post(this.api + '/home/Activity/activ_list',{
        token: localStorage.getItem('token'),
        sort: 1,
        page:1,
        city: val
      })
        .then(res=>{
          if(res.data.code == 1){
            let data = res.data.data.data
            this.hotList = data
            this.LoadingState = false
          }else if(res.data.code == 3){
            this.getActive(val)
          }else if(res.data.code == 0){
            alert(res.data.msg)
          }
        })

    },
    getStory(){
      this.LoadingState2 = true
      this.$http.post(this.api + '/home/Story/story_list',{
        token: localStorage.getItem('token'),
        sort: 2,
        page: 1
      })
        .then(res=>{
          if(res.data.code == 1){
            let data = res.data.data.data
            if(data.length){
              let a = []
              for(let i = 0;i<data.length;i++){
                let user = data[i].user
                let avator = ''
                let zan = ''
                if(data[i].cover_image){
                  if(user.head_image){
                    avator = user.headimage.domain + user.headimage.image_url
                  }else{
                    avator =  '../../static/img/static/user.png'
                  }
                  if(data[i].is_praise){
                    zan = 1
                  }else{
                    zan = 0
                  }
                  a.push({
                    img:data[i].cover.domain + data[i].cover.image_url,
                    address:data[i].province,
                    remark: data[i].title,
                    avator:avator,
                    zan: data[i].praise_num,
                    say: data[i].leavemsg_num,
                    zanIndex:zan,
                    story_id: data[i].story_id,
                    userId: data[i].user_id,
                    leaving_num: data[i].leaving_num
                  })
                }
              }
              this.wonderfulList = a
            }
            this.LoadingState2 = false
          }else if(res.data.code == 3){
            this.getStory()
          }else if(res.data.code == 0){
            alert(res.data.msg)
          }
        })
    },
    reload(){
      this.getBanner()
      this.getVist()
      this.getKind()
      this.sendBuidu()
    },
    sendBuidu(){
      if(window.location.href.indexOf('.top') == -1 && window.location.href.indexOf('localhost') == -1){
        this.$http.post(this.api + '/BaiduPush',{
          token: localStorage.getItem('token'),
          url: encodeURIComponent(window.location.href)
        })
          .then(res=>{
            if(res.data.code == 1){
            }else if(res.data.code == 3){
              this.sendBuidu()
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      }
    },
    //
    getBanner(){
      this.isLoading = true
      this.$http.post(this.api + '/home/Banner/bannerlist',{
        token: localStorage.getItem('token'),
        flag: 0
      })
        .then(res=>{
          if(res.data.code == 1){
            this.bannerList = res.data.data
            this.isLoading = false
          }else if(res.data.code == 3){
            this.getBanner()
          }else if(res.data.code == 0){
            alert(res.data.msg)
            this.isLoading = false
          }
        })
        .catch(error=>{
          this.isLoading = false
        })
    },
    getHotLocal(){
      this.LoadingState = true
      this.$http.post(this.api + '/PopularCity',{
        token: localStorage.getItem('token'),
      })
        .then(res=>{
          if(res.data.code == 1){
            if(res.data.data.length){
              this.hotnav = res.data.data
              this.hotIndex = this.hotnav[0].city
              this.getActive(this.hotIndex)
            }
            this.ishotPlace = false
          }else if(res.data.code == 3){
            this.getHotLocal()
          }else if(res.data.code == 0){
            alert(res.data.msg)
          }
        })
    },

  },
  mounted(){
    window.scroll(0,0)
    let _this = this
    window.addEventListener('scroll',this.showHead)
    _this.language = test()
  },
  destroyed(){
    window.removeEventListener('scroll',this.showHead)
  }
}



