  import '../../static/css/dialog.css'
	export default {
		props:['height','width','title','model',],
		data() {
			return {
				checked:true,
        v:false,
        c:false,
        api:this.GLOBAL.baseURL,
        disabledBtn:true,
        smsTime:60,
        changeTime:'',
        options:[
          {value:0,label:''},
          {value:1,label:''},
          {value:2,label:''}
        ],
        tel_register:{
          mobile:'',
          m_code:'+86()',
          fobid:0,
          sms_code:'',
          sms:'',
          password:'',
          repassword:'',
          flag:'',
        },
        tel_onlogin:{
          m_code:'+86()',
          lmobile:'',
          password:'',
          fobid:'',
          sms:'',
          sms_code:'',
          email:'',
          emailPassword:''
        },
        resetPhone:{
          m_code:'+86()',
          lmobile:'',
          sms_code:'',
          forbid:0,
          sms:'',
          password:''
        },
        countryNumber:[
          {value:1,label:'+86()'}
        ],
        email_Login:{
          email_num:'',
          email_sms:'',
          email_password:'',
          email_Repassword:'',
          flag:'',
          fobid:0,
          sms:''
        },
        emailForm:{
          email:'',
          smsEmail:'',
          emailPass:'',
          emailRePass:'',
          forbid:1,
          sms:'',
        },
        topId:''
			}
		},
		methods:{
		  //
      sendEmail(){
        if(!this.emailForm.email){
          this.$message({
            type: 'info',
            message: ''
          })
        }else{
          this.$http.post(this.api + '/email',{
            token: localStorage.getItem('token'),
            flag: 2,
            toemail: this.emailForm.email
          })
            .then(res=>{
              if(res.data.code == 1){
                this.emailForm.forbid = 1
                this.changeTime = setInterval(()=>{
                  this.smsTime--
                  this.emailForm.sms = ''+this.smsTime+ ''
                  if(this.smsTime <= 0){
                    this.smsTime = 60
                    this.emailForm.forbid = 0
                    this.emailForm.sms = ''
                    clearInterval(this.changeTime)
                  }
                },1000)
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.sendEmail()
                  })
              }
            })
        }
      },
      checkEmail(){
        if(this.email_Login.email_num.length){
          var reg =  /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/
          if(reg.test(this.email_Login.email_num)){
            this.reEmail()
          }else{
            this.$message({
              type: 'error',
              message: '!'
            })
            this.email_Login.fobid = 1
          }
        }
      },
      checkEmailA(){
        if(this.emailForm.email){
          this.$http.post(this.api + '/CheckEmail',{
            token: localStorage.getItem('token'),
            email: this.emailForm.email
          })
            .then(res=>{
              if(res.data.code == 1){
                this.emailForm.email = ''
                this.$message({
                  type: 'error',
                  message: ''
                })
              }else if(res.data.code == 0){
                this.emailForm.forbid = 0
              }
            })
        }
      },
      reEmail(){
        this.$http.post(this.api + '/CheckEmail',{
          token: localStorage.getItem('token'),
          email: this.email_Login.email_num,
        })
          .then(res=>{
            if(res.data.code == 0){
              this.$message({
                type: 'info',
                message:'!'
              })
              this.email_Login.fobid = 1
            }else if(res.data.code == 1){
              this.email_Login.fobid = 0
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.reEmail()
                })
            }
          })
      },
      toWeixin(){
        var baseurl = location.href
       window.open( this.api + '/WechaLogin?token='+ localStorage.getItem('token')+'&baseurl='+encodeURIComponent(baseurl)+'&userId='+this.top_id,'_self')
      },
      toLogin(){
        var baseurl = location.href
        window.open(this.api + "/QqLogin"+'?baseurl='+encodeURIComponent(baseurl)+'&token='+ localStorage.getItem('token')+'&userId='+this.top_id,'_self')
      },
			delDialog(){
				this.$emit('deldialog')
        this.tel_register = {
          mobile:'',
          m_code:'+86()',
          fobid:0,
          sms_code:'',
          sms:'',
          password:'',
          repassword:'',
          flag:'',
        }
        this.tel_onlogin={
          m_code:'+86()',
          lmobile:'',
          password:'',
          fobid:'',
          sms:'',
          sms_code:'',
          email:'',
          emailPassword:''
        }
        this.resetPhone = {
          m_code:'+86()',
          lmobile:'',
          sms_code:'',
          forbid:0,
          sms:'',
          password:''
        }
        this.smsTime = 60
        clearInterval(this.changeTime)
			},
      toPhone(){
        this.$emit('toPhone')
      },
			registerView(){
				this.$emit('registerView')
			},
			loginView(){
				this.$emit('loginView')
			},
			telRegister(){
				this.$emit('telRegister')
			},
			emailRegister(){
				this.$emit('emailRegister')
			},
			forgetPass(){
				this.$emit('forgetPass')
			},
			telReset(){
				this.$emit('telReset')
			},
			emailReset(){
				this.$emit('emailReset')
			},
			checkLogin(){
				this.$emit('checkLogin')
			},
			passLogin(){
				this.$emit('passLogin')
			},
      toEmail_login(){
        this.$emit('emailLogin')
      },
      login(){
			  this.disabledBtn = false
        this.$http.post(this.api + '/email',{
          token: localStorage.getItem('token'),
          toemail: this.email_Login.email_num,
          flag: 1
        })
          .then(res=>{
            if(res.data.code == 1){
              this.email_Login.fobid = 1
              this.changeTime = setInterval(()=>{
                this.smsTime--
                this.email_Login.sms = ''+this.smsTime+ ''
                if(this.smsTime <= 0){
                  this.smsTime = 60
                  this.email_Login.fobid = 0
                  this.email_Login.sms = ''
                  clearInterval(this.changeTime)
                }
              },1000)
            }else if(res.data.code == 3){
              this.login()
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      tel_email(){
        this.$http.post(this.api + '/RegisterEmail',{
          token: localStorage.getItem('token'),
          email: this.email_Login.email_num,
          sms_code: this.email_Login.email_sms,
          password: this.email_Login.email_password,
          repassword: this.email_Login.email_Repassword,
          flag: this.email_Login.flag,
          top_user_id: this.topId,
        })
          .then(res=>{
            if(res.data.code == 1){
              localStorage.setItem('isLogin',true)
              this.$emit('regist',res)

              if( this.email_Login.flag == 1){
                this.$router.push({
                  path: '/Validation',
                  query:{
                    information: 'volunteer'
                  }
                })
              }else if(  this.email_Login.flag == 2){
                this.$router.push( '/authentication')
              }
              this.email_Login ={
                email_num:'',
                email_sms:'',
                email_password:'',
                email_Repassword:''
              }
              if(this.email_Login.flag == 1){
                this.$router.push({
                  path: '/authentication',
                  query:{
                    information: 'volunteer'
                  }
                })
              }else if(this.email_Login.flag == 2){
                this.$router.push( '/authentication')
              }
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.tel_email()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      listenLogin(){
			    if(event.keyCode === 13){
			      this.tel_login()
          }
      },
      listenSend(){
			  if(event.keyCode === 13){
			    this.telSend()
        }
      },
      toEmail(){
        this.$emit('toEmail')
      },
      verifyPhone(){
			  if(this.tel_register.mobile){
          this.$http.post(this.api + '/home/User/validate_mobile',{
            token: localStorage.getItem('token'),
            m_code: parseInt(this.tel_register.m_code),
            mobile:this.tel_register.mobile
          }).then(res=>{
            if(res.data.code == 1){
              this.tel_register.fobid = 0
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                })
            }else{
              this.tel_register.fobid = 1
              alert(res.data.msg)
            }
          })
            .catch(error=>{
              this.tel_register.fobid = 0
            })
        }

      },
      telSend() {
                this.$http.post(this.api + '/home/User/register', {
                  token: localStorage.getItem('token'),
                  mobile: this.tel_register.mobile,
                  m_code: parseInt(this.tel_register.m_code),
                  sms_code: this.tel_register.sms_code,
                  password: this.tel_register.password,
                  repassword: this.tel_register.repassword,
                  flag: this.tel_register.flag,
                  top_user_id:this.topId
                }).then(res => {
                  if (res.data.code == 1){
                    localStorage.setItem('isLogin',true)
                    this.$message({
                      type:'success',
                      message:''
                    })
                    if(this.tel_register.flag == 1){
                      this.$router.push({
                        path: '/Validation',
                        query:{
                          information: 'volunteer'
                        }
                      })
                    }else if( this.tel_register.flag == 2){
                      this.$router.push( '/authentication')
                    }
                    this.$emit('regist',res)
                  }else if(res.data.code == 3){
                    this.$http.post(this.api + '/home/index/token')
                      .then(res=>{
                        localStorage.setItem('token',res.data.data)
                        this.telSend()
                      })
                  }else if(res.data.code == 0){
                    alert(res.data.msg)
                  }
                })

      },
      tel_login(){
			  if(this.tel_onlogin.sms_code == ''){
			    if(this.model == '0'){
                this.$http.post(this.api + '/home/User/login_psw',{
                  token: localStorage.getItem('token'),
                  m_code: parseInt(this.tel_onlogin.m_code),
                  mobile: this.tel_onlogin.lmobile,
                  password: this.tel_onlogin.password
                }).then(res=>{
                  if(res.data.code == 1){
                    this.tel_onlogin.m_code = ''
                    this.tel_onlogin.lmobile = ''
                    this.tel_onlogin.password = ''
                    localStorage.setItem('isLogin',true)
                    this.$emit('onLogin',res)
                    this.smsTime = 60
                    clearInterval(this.changeTime)
                  }else if(res.data.code == 3){
                    this.$http.post(this.api + '/home/Index/token')
                      .then(res=>{
                        localStorage.setItem('token',res.data.data)
                        this.tel_login()
                      })
                  }else if(res.data.code == 0){
                    this.$alert(res.data.msg)
                  }
                })
          }else if(this.model == '8'){
			      this.$http.post(this.api + '/EmailLogin',{
			        token: localStorage.getItem('token'),
              email: this.tel_onlogin.email,
              password: this.tel_onlogin.emailPassword
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.tel_onlogin.email = ''
                  this.tel_onlogin.emailPassword = ''
                  localStorage.setItem('isLogin',true)
                  this.$emit('onLogin',res)
                }else if(res.data.code == 3){
                  this.$http.post(this.api + '/home/Index/token')
                    .then(res=>{
                      localStorage.setItem('token',res.data.data)
                      this.tel_login()
                    })
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
          }
        }else{
              this.$http.post(this.api + '/home/User/login_sms',{
                token: localStorage.getItem('token'),
                mobile: this.tel_onlogin.lmobile,
                sms_code : this.tel_onlogin.sms_code,
                m_code: parseInt(this.tel_onlogin.m_code)
              })
                .then(res=>{
                  if(res.data.code == 1){
                    this.tel_onlogin.m_code = ''
                    this.tel_onlogin.lmobile = ''
                    this.tel_onlogin.password = ''
                    localStorage.setItem('isLogin',true)
                    this.$emit('onLogin',res)
                    this.smsTime = 60
                    clearInterval(this.changeTime)
                  }else if(res.data.code == 3){
                   this.$http.post(this.api + '/home/Index/token')
                     .then(res=>{
                       localStorage.setItem('token',res.data.data)
                       this.tel_login()
                     })
                  }else{
                    this.$alert(res.data.msg)
                  }
                })
        }

      },
      sendSms(){
			  this.$http.post(this.api + '/home/User/send_msg',{
			    token: localStorage.getItem('token'),
          m_code: parseInt(this.tel_register.m_code),
          mobile: this.tel_register.mobile,
          flag:2
        })
          .then(res=>{
            if(res.data.code == 1){

              this.tel_register.fobid = 1
              this.changeTime = setInterval(()=>{
                this.smsTime--
                this.tel_register.sms = ''+this.smsTime+ ''
                if(this.smsTime <= 0){
                  this.smsTime = 60
                  this.tel_register.fobid = 0
                  this.tel_register.sms = ''
                  clearInterval(this.changeTime)
                }
              },1000)
            }
          })
      },

      onloginSmS(){
        this.$http.post(this.api + '/home/User/send_msg',{
          token: localStorage.getItem('token'),
          m_code: parseInt(this.tel_onlogin.m_code),
          mobile: this.tel_onlogin.lmobile,
          flag:3
        })
          .then(res=>{
            if(res.data.code == 1){

              this.tel_onlogin.fobid = 1
              this.changeTime = setInterval(()=>{
                this.smsTime--
                this.tel_onlogin.sms = ''+this.smsTime+ ''
                if(this.smsTime <= 0){
                  this.smsTime = 60
                  this.tel_onlogin.fobid = 0
                  this.tel_onlogin.sms = ''
                  clearInterval(this.changeTime)
                }
              },1000)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  if(res.data.code == 1){
                    localStorage.setItem('token',res.data.data)
                    this.onloginSmS()
                  }
                })
            }else if(res.data.code == 0){
              this.$alert(res.data.msg)
            }
          })
      },
      resetSendSmS(){
        this.$http.post(this.api + '/home/User/send_msg',{
          token: localStorage.getItem('token'),
          m_code: parseInt(this.resetPhone.m_code),
          mobile: this.resetPhone.lmobile,
          flag:1
        })
          .then(res=>{
            if(res.data.code == 1){
              this.resetPhone.fobid = 1
              this.changeTime = setInterval(()=>{
                this.smsTime--
                this.resetPhone.sms = ''+this.smsTime+ ''
                if(this.smsTime <= 0){
                  this.smsTime = 60
                  this.resetPhone.fobid = 0
                  this.resetPhone.sms = ''
                  clearInterval(this.changeTime)
                }
              },1000)
            }else if(res.data.code == 3) {
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  if(res.data.code == 1){
                    localStorage.setItem('token',res.data.data)
                    this.resetSendSmS()
                  }
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      resetPhone_login(){
			  this.$http.post(this.api + '/home/User/find_pwd',{
			    token: localStorage.getItem('token'),
			    m_code: parseInt(this.resetPhone.m_code),
          mobile: this.resetPhone.lmobile,
          sms_code: this.resetPhone.sms_code,
          password: this.resetPhone.password,
        })
          .then(res=>{
            if(res.data.code == 1){
              this.resetPhone.m_code = ''
              this.resetPhone.lmobile = ''
              this.resetPhone.sms_code = ''
              this.resetPhone.password =''
              localStorage.setItem('isLogin',true)
              this.$emit('onLogin',res)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  if(res.data.code == 1){
                    localStorage.setItem('token',res.data.data)
                    this.resetPhone_login()
                  }
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      toEmailL(){
        this.$http.post(this.api + '/FindEmailPwd',{
          token: localStorage.getItem('token'),
          email: this.emailForm.email,
          sms_code: this.emailForm.smsEmail,
          password: this.emailForm.emailPass,
          re_password: this.emailForm.emailRePass
        })
          .then(res=>{
            if(res.data.code == 1){
              localStorage.setItem('isLogin',true)
              this.$emit('onLogin',res)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.toEmailL()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      }
		},
    mounted(){
      var str=location.href; //
      var num=str.indexOf("?")
      str=str.substr(num+1); //   stringvar.substr(start [, length ]
      var arr=str.split("&"); //
      for(var i=0;i < arr.length;i++) {
        num = arr[i].indexOf("=");
        if (num > 0) {
          var name = arr[i].substring(0, num);
          if (name == 'userId') {
            var value = arr[i].substr(num + 1);
            var c = value.indexOf('#')
            value = value.substring(0, c)
            this.topId = value
          }else if(sessionStorage.getItem('Share')){
            this.topId = sessionStorage.getItem('Share')
          }
        }
      }
    },
    destroyed() {
      this.smsTime = 60
		  clearInterval(this.changeTime)
    }
  }



