
import axios from 'axios'
import globalVariable from '../api/global_variable.js'
import {Message} from 'element-ui'
// 
axios.defaults.timeout = 20000
// http
axios.interceptors.request.use(config => {
  return config
}, error => {
  Message.info({message:''})
  return Promise.reject(error)
})


// http
axios.interceptors.response.use(res => {// loading
  const code = res.data.code
  switch (code) {
    case 3:
      axios.post(globalVariable.baseURL + '/home/index/token')
        .then(msg=>{
          localStorage.setItem('token',msg.data.data)
        })
          break;
    case 4:
      axios.post(globalVariable.baseURL + '/home/index/token')
        .then(msg=>{
          localStorage.setItem('token',msg.data.data)
        })
          break;
    case 5:
      Message.info({message:res.data.msg})
          break;
    case 6:
      Message.info({message:res.data.msg})
          break;
    case 7:
      Message.info({message:res.data.msg})
          break;
    case 99:
      Message.info({message:res.data.msg})
          break
  }

  return res
}, error => {
  Message.info({message:''})
  return Promise.reject(error)
})
export default axios





