
import axios from 'axios'
import globalVariable from '../../src/api/global_variable.js'


function getJSSDk(url,shareData){
  axios.post(globalVariable.baseURL + '/WechaShare',{
    token: localStorage.getItem('token'),
    url:url
  })
    .then(res=>{
      if(res.data.code == 1){
        wx.config({
          debug: false, // ,apialertpclogpc
          appId: res.data.data.appId, // 
          timestamp: res.data.data.timestamp, // 
          nonceStr: res.data.data.nonceStr, // 
          signature: res.data.data.signature,// 
          jsApiList: [
            "updateAppMessageShareData",//QQ
            "updateTimelineShareData",//QQ
            ]//] // JS api openlocation
        });
      }
    })
  wx.ready(function(){
     wx.updateAppMessageShareData({
      title: shareData.title,
      desc: shareData.desc,
      link: shareData.linkurl,
      imgUrl: shareData.img,
      trigger: function trigger(res) { },
      success: function success(res) {

      },
      cancel: function cancel(res) {

      },
      fail: function fail(res) {

      }
    });
    wx.updateTimelineShareData({
      title: shareData.title,
      desc: shareData.desc,
      link: shareData.linkurl,
      imgUrl: shareData.img,
      trigger: function trigger(res) { },
      success: function success(res) {

      },
      cancel: function cancel(res) {

      },
      fail: function fail(res) {

      }
    })
  })
  wx.error(function(res){

//configerrorconfigdebugresSPA
  });
}

export default getJSSDk



