  import test from '../../static/js/language'
  import '../../static/css/zan/iconfont.css'
  import loadingImg from './loadingImg'
	export default {
		props:['activity_id','imgUrl','name','score','english','total_time','money','activ_provite','comment_num','remark','type','status','avator','zan','say','address','zanIndex','status','kind','city'],
    data(){
		  return {
		    language:[],
        pubLish:'/publishPage?information='+ this.activity_id,
        defutImg: '../../static/img/static/defult.png',
        userImg:'../../static/img/static/user.png',
        storyImg:'../../static/img/static/defult.png'
      }
    },
    components:{
      loadingImg,
    },
    watch:{
      imgUrl:function(){
        this.change()
      }
    },
    methods:{
      toPublish(){
        this.$emit('toPublish')
      },
      toStory(){
		    this.$emit('toStory')
      },
      addZan(){
        this.$emit('addZan')
      },
      toperson(){
        this.$emit('toperson')
      },
      change(){
        if(this.type == 2){
          var newImg = new Image();
          newImg.src = this.imgUrl;
          var maxWidth = 260
          var maxHeight = 250
          if (newImg.width < maxWidth && newImg.height < maxHeight) {
            newImg.width = maxWidth
            newImg.height = maxHeight
          }
          else //  ,
          {
            if (maxWidth/ maxHeight  <= newImg.width / newImg.height) //  
            {
              newImg.width = maxWidth ;   //
              newImg.height = maxWidth* (newImg.height / newImg.width) ;
            }
            else {   //  
              newImg.width = maxHeight  * (newImg.width / newImg.height) ;
              newImg.height = maxHeight ;   //
            }
          }
          newImg.onerror = () => {  // 
            newImg.src = this.storyImg;
          };
          newImg.onload = () => { // img
            this.storyImg = newImg.src
          }
        }
      }
    },
    mounted(){
		  this.change()
    },
		created(){
		  this.language = test()
		},
	}



