  import LoadingImg from './loadingImg'
	import Dialog from "./dialog.vue";
	import Navslider from "./navslider"
  import Bus from '../assets/eventBus'
	export default {
		props:['type','content'],
		data() {
			return {
				showDialog:false,
        isLoginMess:false,
				height:'340',
				width:'400',
				model:"0",
				showSlider:false,
				isLogin:false,
				inputWidth:300,
        api:this.GLOBAL.baseURL,
        userImg:'',
        isMesage:false,
        someThing:false,
        volunteer:false,
        pageIndex:-1,
        aeditSearch:'',
        isPlayer:false,
        userDefult: '../../static/img/static/user.png' ,
      };
		},
		components:{
			Dialog,
      Navslider,
      LoadingImg
		},
    watch:{
      aeditSearch:function(){
        this.$emit('getVal',this.aeditSearch)
      }
    },
		methods:{
      search(){
        this.$emit('search')
      },
			toAccount(){
				this.$router.push('/account')
			},
			toTravelfunds(){
				this.$router.push('/travelfunds')
			},
			editFocus(){
				this.inputWidth=350
			},
			editBlur(){
				this.inputWidth=300
			},
			toEdit(){
				this.$router.push('/edit')
			},
			delslider(){
				this.showSlider=false
			},
			showHelp(){
				this.showSlider=true
			},
			toExperience(){
				/*this.$router.push('/volunteers')*/
        this.pageIndex = '1'
        this.$router.push('/publishList')

			},
			toHome(){
				this.$router.push('/')
			},
			toVolunteers(){
        this.pageIndex = 1
				this.$router.push('/volunteerList')
			},
			deldialog(){
				this.showDialog=false
			},
      toPhone(){
        this.model="2";
        this.height="402"
      },
      toWish(){
        this.$router.push('/wish')
      },
      toJourney(){
        this.$router.push('/jouerney')
      },
      toTransaction(){
			  this.$router.push('/transaction')
      },
      emailLogin(){
        this.model= '8'
        this.showDialog = true
        this.height='380px'
      },
      toEmail(){
        this.model = '3'
        this.showDialog=true;
        this.height="462"
      },
			toRegister(){
				this.showDialog=true;
				this.model="1"
				this.height="300"
/*        this.$router.push({
          name: 'logion',
          query:{
            information: '2'
          }
        })*/
			},
			toLogin(){
				this.showDialog=true;
				this.model="0"
				this.height="380"
/*			this.$router.push({
        name: 'logion',
        query:{
          information: '1'
        }
      })*/
			},
			registerView(){
				this.model="1";
				this.height="340";
			},
			loginView(){
				this.model="0"
				this.height="380"
			},
			telRegister(){
				this.model="2";
				this.height="402"
			},
			emailRegister(){
				this.model="3";
				this.height="462"
			},
			forgetPass(){
				this.model="4";
				this.height="250"
			},
			telReset(){
				this.model="5";
				this.height="415"
			},
			emailReset(){
				this.model="6";
				this.height="450"
			},
			checkLogin(){
				this.model="7";
				this.height="380"
			},
			passLogin(){
				this.model="0";
				this.height="380"
			},
			toStory(){
				this.$router.push('/story')
			},
      toStoryPublish(){
        this.$router.push('/storyList')
      },
      toMessage(){
			  this.$router.push('/message')
      },
      regist(res) {
        this.isLogin = true
        this.showDialog = false

     
          this.userImg = res.data.data.headimage? res.data.data.headimage.domain + res.data.data.headimage.image_url :this.userDefult

        if(res.data.data.audit_face == 2 && res.data.data.isplanner == 1){
          this.someThing = true
        }
        if(res.data.data.isvolunteer == 1 && res.data.data.audit_idcard == 1 ){
          this.volunteer = true
        }
        localStorage.setItem('userImg',this.userImg)
        this.$emit('reload')
      },
      exitAccount(){
        this.$http.post(this.api+'/home/User/quit',{
          token:localStorage.getItem('token'),
        }).then(res=>{
          if(res.data.code == 1){
            localStorage.removeItem('isLogin')
            localStorage.removeItem('userImg')
            localStorage.removeItem('token')
            this.isLogin = false
            this.isLoginMess = false
            this.someThing = false
            this.volunteer = false
            this.getToken()
            this.$router.push('/')
          }else if(res.data.code == 3){
            this.exitAccount()
          }else if(res.data.code == 0){
            alert(res.data.msg)
          }
        })
      },
      onLogin(res){
        this.showDialog = false
        this.isLogin = true
        if(res.data.data.headimage){
          this.userImg = res.data.data.headimage.domain + res.data.data.headimage.image_url
          localStorage.setItem('userImg',this.userImg)
        }else{
          this.userImg = '../../static/img/static/user.png'
          localStorage.setItem('userImg',this.userImg)
        }
        if(res.data.data.isplanner == 1 && res.data.data.audit_face == 2 ){
          this.someThing = true
        }
        if(res.data.data.isvolunteer == 1 && res.data.data.audit_idcard == 1){
          this.volunteer = true
        }
        this.$emit('reload',res)
        this.$emit('onreload',res)
      },
      quitExperience(){
			  this.$router.push('/publishList')
      },
      getMesage(){
			  this.$http.post(this.api +'/home/Sysmsg/noread',{
			    token: localStorage.getItem('token')
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data
              if(data.chat_count === 0&&data.notice_count === 0&&data.sys_count === 0&& data.praise_count === 0 && data.comment_count === 0){
                this.isMesage = false
              }else{
                this.isMesage = true
              }
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getMesage()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      toCehua(){
			  this.$router.push('/myPlay')
      },
      toZhiyuan(){
			  this.$router.push('/myWant')
      },
      getToken(){ 
        if(localStorage.getItem('token')){
          if(localStorage.getItem('isLogin')){
            this.getMesage()
            this.isLogin = localStorage.getItem('isLogin')
            // this.$emit('reload')
            // this.getNoReadAll()
            // this.userImg = localStorage.getItem('userImg')
            this.$http.post(this.api + '/home/User/get_user',{
              token: localStorage.getItem('token')
            })
              .then(res=>{
                if(res.data.code == 1){
                  if(res.data.data.length > 0){
                    if(res.data.data[0].headimage){
                      this.userImg = res.data.data[0].headimage.domain + res.data.data[0].headimage.image_url
                    }else{
                      this.userImg = '../../static/img/static/user.png'
                    }
                    if(res.data.data[0].isplanner == 1 && res.data.data[0].audit_face == 2 ){
                      this.someThing = true
                    }
                    if(res.data.data[0].isvolunteer == 1 && res.data.data[0].audit_idcard == 1){
                      this.volunteer = true
                    }
                    this.$emit('reload',res)
                    this.getNoReadAll()
                  }
                }else if(res.data.code == 3){
                  this.$http.post(this.api + '/home/Index/token')
                    .then(res=>{
                      localStorage.setItem('token',res.data.data)
                      this.$emit('reload')
                      localStorage.removeItem('isLogin')
                      localStorage.removeItem('userImg')
                    })
                }else{
                  this.$emit('reload')
                  localStorage.removeItem('isLogin')
                  localStorage.removeItem('userImg')
                }
              })
          }else{
            this.$http.post(this.api + '/home/User/get_user',{
              token: localStorage.getItem('token')
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.isLogin = true
                    if(res.data.data[0].headimage){
                      this.userImg = res.data.data[0].headimage.domain + res.data.data[0].headimage.image_url
                    }else{
                      this.userImg = '../../static/img/static/user.png'
                    }
                    
                    if( res.data.data[0].isplanner == 1 && res.data.data[0].audit_face == 2){
                      this.someThing = true
                    }
                    if(res.data.data[0].isvolunteer == 1 && res.data.data[0].audit_idcard == 1){
                      this.volunteer = true
                    }
                   localStorage.setItem('isLogin',true)
                    this.$emit('reload',res)
                    this.getNoReadAll()
                }else if(res.data.code == 3 || res.data.code == 2){
                  this.$http.post(this.api + '/home/Index/token')
                    .then(res=>{
                      localStorage.setItem('token',res.data.data)
                      this.$emit('reload')
                      localStorage.removeItem('isLogin')
                      localStorage.removeItem('userImg')
                    })
                }else{
                  this.$emit('reload')
                  localStorage.removeItem('isLogin')
                  localStorage.removeItem('userImg')
                }
              })
          }
        }else{
          this.isLogin = false
          this.$http.post(this.api + '/home/Index/token')
            .then(res=>{
              localStorage.setItem('token',res.data.data)
              this.$emit('reload')
              this.getNoReadAll()
            })
        }
      },
      getLang(){
        var type = navigator.appName;
        if (type == "Netscape"){
          var lang = navigator.language;//IE
        }else{
          var lang = navigator.userLanguage;//IE5+ == navigator.systemLanguage
        }
        var lang = lang.substr(0, 2);//
        alert(lang)
      },
      getNoReadAll(){
        this.$http.post(this.api + '/PlannerNoRead',{
          token: localStorage.getItem('token'),
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data
              if(data.complete_num != 0 || data.enroll_num != 0||data.order_num != 0 || data.refund_num != 0){
                this.isPlayer = true
              }
            }else if(res.data.code == 3){
              this.getNoReadAll()
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
		},
    mounted(){
		  this.getToken()
    },
    created(){
      Bus.$on('userImage',(msg)=>{
        this.userImg = msg
      })
      Bus.$on('changeContent',(msg)=>{
        this.aeditSearch = msg
      })
      Bus.$on('nore',(msg)=>{
        this.isMesage = msg
      })
    }
  }



